
.App {

}

.Personal {
  text-align: center;
  max-width: 1150px;
  height: 750px;
  margin: auto;
  /*background-color: yellow;*/
  display: flex;
  flex-direction: column;
}

.Personal-nav {
  display: flex;
  flex-direction: row;
  margin-top: 5px;
  /*margin-left: 10px;*/
  /*margin-right: 10px;*/
}

.Personal-nav-left-btns {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;
  text-align: right;
}

.Personal-nav-left-btns a {
  text-color: 000;
  text-decoration: none;
  padding-right: 40px;
}

.Personal-nav-left-btns a:visited {
  color: #000;
}

.Personal-nav-social {
  margin-left: auto;
  margin-right: 0;

}

.Personal-nav-social a {
  margin-left: 10px;
}

.Personal-nav-social button {
  margin-left: 10px;
}

.vk {
  background: url("svg/vk.svg") no-repeat;
  border: none;
  width: 30px;
  height: 30px;
}

.github {
  background: url("svg/github.svg") no-repeat;
  border: none;
  width: 30px;
  height: 30px;

}

.linkedin {
  background: url("svg/linkedin.svg") no-repeat;
  border: none;
  width: 30px;
  height: 30px;

}

.spotify {
  background: url("svg/spotify.svg") no-repeat;
  border: none;
  width: 30px;
  height: 30px;

}

.telegram {
  background: url("svg/telegram.svg") no-repeat;
  border: none;
  width: 30px;
  height: 30px;

}

.personal-desc {
  margin-left: 120px;
  display: flex;
  flex-direction: column;
  /*border-style: solid;*/
  /*border-color: black;*/
}

.personal-desc-welcome {
  margin-top: 20px;
  text-align: left;
  max-width: 800px;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 44px;
  line-height: 60px;
}

.personal-desc-about {
  margin-top: 20px;
  text-align: left;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 23px;
}

.Personal-info {
  margin-top: 140px;
  display: flex;
  flex-direction: row;
}

.aqent {
  color: #FF0000;
}

.avatar {
  margin-left: auto;
  margin-right: 40px;
}

.download-btn {
  margin-top: 40px;
  text-align: left;
}

.download-btn a {
  background: #FF6464;
  border: 6px solid #FF6464;
  border-radius: 5px;
  color: #fff;
  text-decoration: none;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 29px;
}

.avatar img {
  border-radius: 40px;
}

.Skills-background {
  background-color: #A3CEDB;
  height: 920px;
}

